@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@0,400;0,500;0,600;0,700;1,200;1,300;1,400;1,500;1,700&family=Montserrat:wght@400;600;700;800&family=Roboto&family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  padding: 0;

  /* font-family: "Montserrat", sans-serif; */
  /* font-family: "Roboto", sans-serif; */
  /* font-family: "Space Mono", monospace; */
  font-family: "JetBrains Mono", monospace;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #011627;
  color: #fff;

  word-spacing: 2px;
  letter-spacing: 1px;
}

/* ::-webkit-scrollbar {
  width: 0;
} */

/* полоса прокрутки (скроллбар) */
::-webkit-scrollbar {
  width: 6px; /* ширина для вертикального скролла */
  height: 8px; /* высота для горизонтального скролла */
  background-color: #022440ff;
}

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
  background-color: #011627;
  border-radius: 20px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #004242ff;
}



